#CaseStudy {
	padding-top: 60px;
	padding-bottom: 45px;
}
#case-title img,
#case-title h3 {
	display: inline-block;
	vertical-align: top;
}
#case-title img {
	width: 75px;
}
#case-title h3 {
	height: 25px;
	margin: 25px;
}
#case-title hr {
	margin-bottom: 0;
	margin-top: 18px;
}
@media (max-width: 460px) {
	#case-title h3 {
		margin: 25px 0 25px 0;
	}
}



